import { defineComponent, ref, computed } from 'vue';
import { getGroups } from '@/service/groups/getgroups';
import { getNodeInfo } from '@/service/node/getnodeinfo';
import { useStore } from '@/store';
import { useI18n } from 'vue-i18n';
import { langType } from '../index';
export default defineComponent({
  emits: ['foldChange'],

  setup(props, {
    emit
  }) {
    const store = useStore();
    const isFold = ref(false);

    const getgroups = () => {
      getGroups().then(res => {
        console.log(res);
      });
    };

    const getnodeinfo = () => {
      getNodeInfo().then(res => {
        console.log(res);
      });
    };

    const handleFoldClick = () => {
      isFold.value = !isFold.value;
      emit('foldChange', isFold.value);
    };

    const language = computed(() => store.state.lang.language);
    const {
      locale
    } = useI18n();

    const handleCommand = value => {
      console.log(value);
      locale.value = value;
      store.commit('CHANGE_LANGUAGE', value);
    };

    return {
      isFold,
      handleFoldClick,
      getgroups,
      getnodeinfo,
      handleCommand,
      language,
      langType
    };
  }

});